<template>
  <v-stepper alt-labels v-model="e1">
    <!-- START OF STEPPER HEADER-->
    <v-stepper-header>
      <template v-for="(infoContent, infoIndex) in info">
        <v-stepper-step
          :complete="e1 > infoIndex"
          :step="infoIndex + 1"
          :key="infoContent.step"
        >
          <div v-if="e1 - 1 == infoIndex"></div>
        </v-stepper-step>

        <v-divider v-if="infoIndex < info.length - 1" :key="infoIndex" />
      </template>
    </v-stepper-header>
    <!-- END OF STEPPER HEADER-->

    <!-- START OF STEPPER CONTENT-->
    <v-stepper-items>
      <v-stepper-content
        v-for="(infoContent, infoIndex) in info"
        :key="infoIndex"
        :step="infoIndex + 1"
      >
        <v-container
          v-for="(item, itemIndex) in infoContent.item"
          :key="itemIndex"
        >
          <div class="text-h5" v-text="item.itemHeader"></div>

          <br />
          <div class="text-subtitle-2" v-text="item.itemTitle"></div>
          <v-textarea
            :value="item.itemModel"
            @change="(v) => (item.itemModel = v)"
            outlined
          >
          </v-textarea>
          <v-divider></v-divider>
        </v-container>
        <v-card-actions>
          <v-btn v-if="infoIndex > 0 && infoIndex < info.length - 1"
            >Back
          </v-btn>
          <!-- @click="previous" -->
          <v-spacer></v-spacer>
          <v-btn color="primary" @click="next()">Next</v-btn>
        </v-card-actions>
      </v-stepper-content>
    </v-stepper-items>
    <!-- END OF STEPPER CONTENT-->
  </v-stepper>
</template>

<script>
import { mapGetters, mapActions } from "vuex";

export default {
  data() {
    return {
      e1: 1,
      name: "FormThreeCom",
      info: [
        {
          step: "Step 1",
          item: [
            {
              itemHeader: "1. Name of LGU and Income Class",
              // itemTitle: "Name of LGU and Income Class",
              itemName: "lguName",
              itemModel: "",
            },
            {
              itemHeader: "2. Date Received",
              itemName: "receiveDate",
              itemModel: "",
            },
            {
              itemHeader: "3. LGU Profile",
              itemTitle: "No. of barangays (Urban & Rural)",
              itemName: "noBarangay",
              itemModel: "",
            },
            {
              itemTitle: "Population & 10 year Projection",
              itemName: "popProjection",
              itemModel: "",
            },
            {
              itemTitle: "Area in has",
              itemName: "areaHas",
              itemModel: "",
            },
          ],
        },
        {
          step: "Step 2",
          item: [
            {
              itemHeader: "Current SWM Condition",
              itemTitle: "Organization (SWM Office and Board )",
              itemName: "organization",
              itemModel: "",
            },
            {
              itemTitle: "Number of Staff",
              itemName: "numStaff",
              itemModel: "",
            },
            {
              itemTitle: "Collection Equipment",
              itemName: "colEquip",
              itemModel: "",
            },
            {
              itemTitle: "MRF",
              itemName: "mrf",
              itemModel: "",
            },
            {
              itemTitle: "Final disposal site",
              itemName: "finalDisposal",
              itemModel: "",
            },
            {
              itemTitle: "Waste Quantity",
              itemName: "wasteQuan",
              itemModel: "",
            },
            {
              itemTitle: "Waste Composition",
              itemName: "wasteCom",
              itemModel: "",
            },
            {
              itemTitle: "Expenditure on SWM",
              itemName: "expenditure",
              itemModel: "",
            },
            {
              itemTitle: "Annual Budget of the LGU",
              itemName: "annualBud",
              itemModel: "",
            },
          ],
        },
        {
          step: "Step 3",
          item: [
            {
              itemHeader: "5. Future Plan",
              itemTitle: "Source Reduction",
              itemName: "sourceRed",
              itemModel: "",
            },
            {
              itemTitle: "Collection",
              itemName: "collection",
              itemModel: "",
            },
            {
              itemTitle: "Transfer",
              itemName: "transfer",
              itemModel: "",
            },
            {
              itemTitle: "Processing",
              itemName: "processing",
              itemModel: "",
            },
            {
              itemTitle: "Disposal",
              itemName: "disposal",
              itemModel: "",
            },
            {
              itemTitle: "IEC",
              itemName: "iec",
              itemModel: "",
            },
            {
              itemTitle: "Special Waste",
              itemName: "specWaste",
              itemModel: "",
            },
            {
              itemTitle: "Expenditure on SWM",
              itemName: "expenditure",
              itemModel: "",
            },
            {
              itemTitle: "Annual Budget of the LGU",
              itemName: "annualBud",
              itemModel: "",
            },
          ],
        },
        {
          step: "Step 4",
          item: [
            {
              itemHeader:
                "6. Waste Diversion Target (Baseline and 10-Year projection)",
              // itemTitle: "Name of LGU and Income Class",
              itemName: "wasteDiv",
              itemModel: "",
            },
            {
              itemHeader: "7. Financing Aspect",
              itemTitle: "Funding Source/Options",
              itemName: "fundingSource",
              itemModel: "",
            },
            {
              itemTitle: "Expenditures/Investment",
              itemName: "expenditure",
              itemModel: "",
            },
            {
              itemTitle: "Repayment Plan/Cost Recovery System",
              itemName: "repaymentPLan",
              itemModel: "",
            },
          ],
        },
      ],
    };
  },
  created() {
    // let id2 = this.$route.query.id;
    //console.log(id2);
    if (this.$route.query.form_id != null) {
      this.fetchForm2({
        id: this.$route.query.form_id,
      }).then(() => {
        this.setForm2Details();
      });
    }
  },
  methods: {
    ...mapActions(["fetchForm2", "createForm2"]),
    next() {
      this.e1++;
    },
    submit() {
      this.createForm2({
        header_id: this.$route.query.id,
        indicator_com_1_1: this.indicator[0].radio1,
        indicator_rem_1_1: this.indicator[0].remarks1,
        indicator_com_1_2: this.indicator[0].radio2,
        indicator_rem_1_2: this.indicator[0].remarks2,
        indicator_com_1_3: this.indicator[0].radio3,
        indicator_rem_1_3: this.indicator[0].remarks3,
        indicator_com_1_4: this.indicator[0].radio6,
        indicator_rem_1_4: this.indicator[0].remarks4,
        memo_agreement: this.indicator[0].radio4,
        contract: this.indicator[0].radio5,
        disposal_area: this.indicator[0].radio7,

        indicator_com_2_1: this.indicator[1].radio1,
        indicator_rem_2_1: this.indicator[1].remarks1,

        indicator_com_3_1: this.indicator[2].radio1,
        indicator_com_3_2: this.indicator[2].radio2,
        indicator_rem_3_1: this.indicator[2].remarks1,

        indicator_com_4_1: this.indicator[3].radio1,
        indicator_rem_4_1: this.indicator[3].remarks1,

        indicator_com_5_1: this.indicator[4].radio1,
        indicator_rem_5_1: this.indicator[4].remarks1,
        indicator_com_5_2: this.indicator[4].radio2,
        indicator_rem_5_2: this.indicator[4].remarks2,

        indicator_com_6_1: this.indicator[5].radio1,
        indicator_rem_6_1: this.indicator[5].remarks1,

        indicator_com_7_1: this.indicator[6].radio1,
        indicator_rem_7_1: this.indicator[6].remarks1,
      });
    },

    setForm2Details() {
      let newAllChapter = JSON.stringify(this.allForm2);
      let new_data = JSON.parse(newAllChapter);

      this.indicator[0].radio1 = new_data.indicator_com_1_1;
      this.indicator[0].radio2 = new_data.indicator_com_1_2;
      this.indicator[0].radio3 = new_data.indicator_com_1_3;
      this.indicator[0].radio4 = new_data.memo_agreement;
      this.indicator[0].radio5 = new_data.contract;
      this.indicator[0].radio6 = new_data.indicator_com_1_4;
      this.indicator[0].radio7 = new_data.disposal_area;
      this.indicator[0].remarks1 = new_data.indicator_rem_1_1;
      this.indicator[0].remarks2 = new_data.indicator_rem_1_2;
      this.indicator[0].remarks3 = new_data.indicator_rem_1_3;
      this.indicator[0].remarks4 = new_data.indicator_rem_1_4;

      this.indicator[1].radio1 = new_data.indicator_com_2_1;
      this.indicator[1].remarks1 = new_data.indicator_rem_2_1;

      this.indicator[2].radio1 = new_data.indicator_com_3_1;
      this.indicator[2].radio2 = new_data.indicator_com_3_1;
      this.indicator[2].remarks1 = new_data.indicator_rem_3_1;

      this.indicator[3].radio1 = new_data.indicator_com_4_1;
      this.indicator[3].remarks1 = new_data.indicator_rem_4_1;

      this.indicator[4].radio1 = new_data.indicator_com_5_1;
      this.indicator[4].radio2 = new_data.indicator_com_5_2;
      this.indicator[4].remarks1 = new_data.indicator_rem_5_1;
      this.indicator[4].remarks2 = new_data.indicator_rem_5_2;

      this.indicator[5].radio1 = new_data.indicator_com_6_1;
      this.indicator[5].remarks1 = new_data.indicator_rem_6_1;

      this.indicator[6].radio1 = new_data.indicator_com_7_1;
      this.indicator[6].remarks1 = new_data.indicator_rem_7_1;
    },
  },
  computed: {
    ...mapGetters(["allForm2"]),
  },
  watch: {},
};
</script>
